<template>
    <div>
        <van-list v-bind="$attrs" finished-text="没有更多了"
        :finished="finished"
        :error.sync="error"
        error-text="请求失败，点击重新加载"
        @load="getList">
            <slot></slot>
        </van-list>
    </div>
</template>
<script>
import { List } from 'vant';
export default {
    components: {
        [List.name]: List
    },
    data() {
        return {
            error: false,
            loading: false,
            finished: false,
            total: 0,
        }
    },
    methods: {
        getList() {
            this.loading = true;
            const params = {
                method: this.$attrs.method || 'post',
                url: this.$attrs.url,
            };
            if (this.$attrs.method == 'post') {
                params.data = {...this.$attrs.params}
            } else {
                params.params = {...this.$attrs.params}
            }
            this.$axios(params).then(res => {
                debugger
                this.loading = false;
                this.total = res.total;
                const list = res.list
                if (!res.pages || !res.list.length) {
                    // 没有数据了
                    this.finished = true;
                }
                console.log(res)
                this.$emit('get-data', list, res)
            }).catch(error => {
                this.error = true
                console.log(error)
            })
        }
    }
}
</script>